import { PersonAdd, Settings, Logout } from "@mui/icons-material";
import {
  Stack,
  Paper,
  MenuList,
  MenuItem,
  Button,
  Popper,
  Grow,
  ClickAwayListener,
  IconButton,
  Avatar,
  Tooltip,
  Box,
  Divider,
  ListItemIcon,
  Menu,
} from "@mui/material";
import React from "react";
import { useTypedSelector } from "../../../../hooks/useTypedSelector";
import "./UserMenu.css";
import { useActions } from "../../../../hooks/useActions";
import { useNavigate } from "react-router-dom";

function UserMenu(): JSX.Element {
  const { tryLogout, clearCoupons, setCustomers, setCompanies } = useActions();
  const state = useTypedSelector((state) => state);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const navigate = useNavigate();

  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  // const handleLogout = () => {
  //   tryLogout();
  //   navigate("/");
  // };

  // const handleMyAcount = () => {
  //   navigate("/user");
  // };

  return (
    <React.Fragment>
      <Box>
        <Tooltip title="My Acount">
          <IconButton
            onClick={handleClick}
            size="small"
            sx={{ ml: 2 }}
            aria-controls={open ? "account-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
          >
            <Avatar sx={{ width: 32, height: 32 }}>
              {state.users.userName[0].toUpperCase()}
            </Avatar>
          </IconButton>
        </Tooltip>
      </Box>
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: "visible",
            filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
            mt: 1.5,
            "& .MuiAvatar-root": {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            "&:before": {
              content: '""',
              display: "block",
              position: "absolute",
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: "background.paper",
              transform: "translateY(-50%) rotate(45deg)",
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: "right", vertical: "top" }}
        anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
      >
        {state.users.userRole !== "admin" && (
          <MenuItem
            onClick={() => {
              navigate("/my-account");
            }}
          >
            <Avatar /> My account
          </MenuItem>
        )}
        <Divider />
        <MenuItem
          onClick={() => {
            tryLogout();
            clearCoupons();
            setCustomers([]);
            setCompanies([]);
            navigate("/");
          }}
        >
          <ListItemIcon>
            <Logout fontSize="small" />
          </ListItemIcon>
          Logout
        </MenuItem>
      </Menu>
    </React.Fragment>
  );
}

export default UserMenu;
